.main-favorite.favorite {
  overflow-x: scroll;
}

.category-button-wrap {
  width: 100%;
}

.category-button-list {
  gap: 12px;
  display: flex;
}

.category-button-button {
  color: var(--primary-text-color-03);
  border-color: rgba(5, 5, 5, .2);
  padding-left: 18px;
  padding-right: 18px;
  line-height: 1.28571;
}

.category-button-button:hover, .category-button-button:focus {
  background-color: var(--main-color);
}

@media screen and (min-width: 768px) {
  .category-button-button {
    font-size: 16px;
    line-height: 1.25;
  }
}

.no-recipe-icon {
  justify-content: center;
  margin-bottom: 24px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .no-recipe-icon {
    margin-bottom: 32px;
  }
}

.no-recipes {
  text-align: center;
  margin: 0 auto;
  font-size: 14px;
  line-height: 18px;
}

@media screen and (min-width: 768px) {
  .no-recipes {
    width: 481px;
    font-size: 16px;
    line-height: 24px;
  }
}

